import store from "../store";
import axios from "axios";
import beWritten from "../components/beWrriten";
import { useNavigate } from "react-router-dom";
function edit(editType, content, content1 = "", content2 = "") {
  let tranObj = {};
  axios.defaults.withCredentials = true;
  switch (editType) {
    case "Name":
      tranObj = { newUserName: content };
      break;
    case "Height":
      tranObj = { newHeight: content };
      break;
    case "Weight":
      tranObj = { newWeight: content };
      break;
    case "Workout":
      tranObj = { newWorkout: content };
      break;
    case "Icon":
      tranObj = { newIcon: content };
      break;
    case "AddComment":
      tranObj = { newUserComment: content, dishId: content1 };
      break;
    case "EditComment":
      // console.log(content2);
      tranObj = {
        newUserComment: content,
        dishId: content1,
        commentId: content2,
      };
      break;
    case "DeleteComment":
      tranObj = {
        dishId: content,
        commentId: content1,
      };
      console.log(tranObj);
      break;
    case "Dishes":
      tranObj = { newUserDishes: content };
      break;
    case "Psd":
      tranObj = { newPsd: content, originPsd: content1 };
      break;
  }
  axios
    .post("http://47.110.63.20:7777/api/users/editUser" + editType, tranObj)
    .then((res) => {
      if (res.data.error_code === 1) {
        alert(res.data.msg);
      } else {
        alert("");
      }
      console.log(res);
    })
    .catch((err) => console.log("Couldn't fetch data. Error: " + err));
}
// 
export async function login(userName, userPsd) {
  let result;
  // cookie
  axios.defaults.withCredentials = true;
  await axios
    .post("http://47.110.63.20:7777/api/users/login", {
      userName,
      userPsd,
    })
    .then((res) => {
      if (res.data.error_code === 1) {
        alert(res.data.msg);
      } else {
        alert("");
      }
      result = res;
    })
    .catch((err) => {
      console.log("Couldn't fetch data. Error: " + err);
      result = err;
    });
  return result;
}
