// 
export async function issue(
  userName,
  userHeight,
  userWeight,
  userWorkout,
  userPsd
) {
  let result;
  if (
    beWritten(userName, "userName") ||
    beWritten(userHeight, "userHeight") ||
    beWritten(userWeight, "userWeight") ||
    beWritten(userWorkout, "userWorkout") ||
    beWritten(userPsd, "userPsd")
  ) {
    return;
  }
  await axios
    .post("http://47.110.63.20:7777/api/users/", {
      userName,
      userHeight,
      userWeight,
      userWorkout,
      userPsd,
    })
    .then((res) => {
      if (res.status != 201) {
        alert(res.data.msg);
        return;
      }
      result = res;
    })
    .catch((err) => console.log("Couldn't fetch data. Error: " + err));
  return result;
}
