export async function editDish(dishName, dishTags, dishStep, dishId) {
  axios.defaults.withCredentials = true;
  let answer = { result: false, res: "" };
  await axios
    .post("http://47.110.63.20:7777/api/dishes/editDish", {
      dishName,
      dishTags,
      dishStep,
      dishId,
    })
    .then((res) => {
      if (res.data.error_code === 1) {
        answer.res = res;
        alert(res.data.msg);
      } else {
        alert(",");
        answer.result = true;
        answer.res = res;
      }
    })
    .catch((error) => {
      alert(error);
    });
  return answer;
}
